import { Component, OnInit } from '@angular/core';
import { CompanyDepartemenService, CompanyJabatanService, CompanyKantorService, CompanyDivisiService, DirektoriKalenderPerusahaanService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Jabatan, Kantor, Departemen, Divisi, Kalender_perusahaan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriKalenderPerusahaanService: DirektoriKalenderPerusahaanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyJabatanService: CompanyJabatanService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private activatedRoute: ActivatedRoute,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    data: Kalender_perusahaan

    id_kalender_perusahaan: string = ''

    listJabatan: Jabatan[] = []
    listKantor: Kantor[] = []
    listDepartemen: Departemen[] = []
    listDivisi: Divisi[] = []


    ngOnInit() {
        this.id_kalender_perusahaan= this.activatedRoute.snapshot.paramMap.get('id_kalender_perusahaan');
        this.getPreeData()
        this.getData()
        this.form()

    }

    getPreeData() {
        this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
            this.listDepartemen = data.data
        })

        this.companyDivisiService.getCompanyDivisis().subscribe(data => {
            this.listDivisi = data.data
        })

        this.companyJabatanService.getCompanyJabatans().subscribe(data => {
            this.listJabatan = data.data
        })

        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.berlaku_utk_departemen,
                status: 'Active'
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    id_acara: string = "";
    getData() {
        this.spinner.show()
        this.direktoriKalenderPerusahaanService.getDirektoriKalenderPerusahaansId(this.id_kalender_perusahaan).subscribe(
            data => {
                let xx= data.data;
                xx.berlaku_untuk_kantor = JSON.parse(xx.berlaku_untuk_kantor)
                this.dataForm.setValue(xx)
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Kalender_perusahaan>
    form() {
        this.dataForm = this.fb.group<Kalender_perusahaan>(
            {
                nama_hari: new FormControl('', [Validators.required]),
                id_kalender_perusahaan: new FormControl(''),
                id_dokumen: new FormControl(''),
                keterangan: new FormControl(''),
                kategori_hari: new FormControl(''),
                berlaku_untuk_kantor: new FormControl(''),
                berlaku_utk_departemen: new FormControl(''),
                berlaku_utk_divisi: new FormControl(''),
                berlaku_utk_tipe_absensi: new FormControl(''),
                berlaku_utuk_jabatan: new FormControl(''),
                kebijakan: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_user: new FormControl(''),
                tanggal: new FormControl(''),
            }
        )
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.direktoriKalenderPerusahaanService.putDirektoriKalenderPerusahaansId({id: this.id_kalender_perusahaan, body: this.data}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/kalender/kalender-perusahaan/list'])
    }


}
