import { Component, OnInit } from '@angular/core';
import { DirektoriKalenderPerusahaanService } from 'src/app/sdk/hr/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Kalender_perusahaan } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriKalenderPerusahaanService: DirektoriKalenderPerusahaanService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit(){
        this.getData()
    }

    sortKey: string | null = '';
    sortValue: string | null = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getData();
    }
    search: string = "";

    listOfDisplayData: Kalender_perusahaan[] = []
    listOfData: Kalender_perusahaan[] = []
    getData() {
        this.spinner.show()
        this.direktoriKalenderPerusahaanService.getDirektoriKalenderPerusahaans(JSON.stringify({sortKey: this.sortKey, sortValue: this.sortValue, search: this.search})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()

            }
        )
    }

    reset(): void {
        this.search = '';
        this.getData();
    }

    delete(id)
    {
        this.spinner.show();
        this.direktoriKalenderPerusahaanService.deleteDirektoriKalenderPerusahaansId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete()
    {
        this.nzMessageService.info('click cancel');
    }


}
