import { Component, OnInit } from '@angular/core';
import { DirektoriKalenderPerusahaanService, CompanyDepartemenService, CompanyJabatanService, CompanyKantorService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Kalender_perusahaan, Jabatan, Kantor, Departemen, Divisi } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private direktoriKalenderPerusahaanService: DirektoriKalenderPerusahaanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyJabatanService: CompanyJabatanService,
        private companyKantorService: CompanyKantorService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    data: Kalender_perusahaan

    listJabatan: Jabatan[] = []
    listKantor: Kantor[] = []
    listDepartemen: Departemen[] = []
    listDivisi: Divisi[] = []


    ngOnInit() {
        this.getPreeData()
        this.form()

    }

    getPreeData() {
        this.companyDepartemenService.getCompanyDepartemens().subscribe(data => {
            this.listDepartemen = data.data
        })

        this.companyJabatanService.getCompanyJabatans().subscribe(data => {
            this.listJabatan = data.data
        })

        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })
    }

    getDivisi(){
        this.companyDivisiService.getCompanyDivisis(JSON.stringify({id_departemen: this.dataForm.value.berlaku_utk_departemen})).subscribe(data => {
            this.listDivisi = data.data
        })
    }


    dataForm: FormGroupTypeSafe<Kalender_perusahaan>
    form() {
        this.dataForm = this.fb.group<Kalender_perusahaan>(
            {
                nama_hari: new FormControl('', [Validators.required]),
                keterangan: new FormControl('',),
                kategori_hari: new FormControl('',),
                tanggal: new FormControl('',),
                berlaku_untuk_kantor: new FormControl('',),
                berlaku_utk_departemen: new FormControl('',),
                berlaku_utk_divisi: new FormControl('',),
                berlaku_utk_tipe_absensi: new FormControl('',),
                berlaku_utuk_jabatan: new FormControl('',),
                kebijakan: new FormControl('',),
            }
        )
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.direktoriKalenderPerusahaanService.postDirektoriKalenderPerusahaans(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/kalender/kalender-perusahaan/list'])
    }


}
