import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DirektoriKalenderPerusahaanService, CompanyJabatanService, CompanyKantorService, CompanyDepartemenService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { V_pegawai_posisi, Kalender_perusahaan, Jabatan, Kantor, Departemen, Divisi } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private direktoriKalenderPerusahaanService: DirektoriKalenderPerusahaanService,
        private companyJabatanService: CompanyJabatanService,
        private companyKantorService: CompanyKantorService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyDivisiService: CompanyDivisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    id_kalender_perusahaan: string = ''

    pegawai_posisi: V_pegawai_posisi = {}
    kalender: Kalender_perusahaan = {}

    jabatan: Jabatan= {}
    kantor: Kantor= {}
    departemen: Departemen= {}
    divisi: Divisi= {}

    ngOnInit() {
        this.id_kalender_perusahaan = this.routerActive.snapshot.paramMap.get('id_kalender_perusahaan')
        console.log(this.id_kalender_perusahaan)
        this.getKalender();
    }
    getKalender() {
        this.spinner.show()
        this.direktoriKalenderPerusahaanService.getDirektoriKalenderPerusahaansId(this.id_kalender_perusahaan).subscribe(
            response => {
                this.kalender = response.data
                this.getJabatan(response.data.berlaku_utuk_jabatan)
                this.getKantor(response.data.berlaku_untuk_kantor)
                this.getDepartemen(response.data.berlaku_utk_departemen)
                this.getDivisi(response.data.berlaku_utk_divisi)
                this.spinner.hide()
            }
        )

    }

    getJabatan(berlaku_utuk_jabatan) {
        this.companyJabatanService.getCompanyJabatansId(berlaku_utuk_jabatan).subscribe(
            response => {
                this.jabatan = response.data

            }
        )
    }
    getKantor(berlaku_untuk_kantor) {
        this.companyKantorService.getCompanyKantorsId(berlaku_untuk_kantor).subscribe(
            response => {
                this.kantor = response.data
            }
        )
    }
    getDepartemen(berlaku_utk_departemen) {
        this.companyDepartemenService.getCompanyDepartemensId(berlaku_utk_departemen).subscribe(
            response => {
                this.departemen = response.data
            }
        )
    }
    getDivisi(berlaku_utk_divisi) {
        this.companyDivisiService.getCompanyDivisisId(berlaku_utk_divisi).subscribe(
            response => {
                this.divisi = response.data
            }
        )
    }


    goToBack() {

        this.router.navigate(['/hr/kalender/kalender-perusahaan/list'])
    }


}
